import { FORMS_FORM_WORKSPACE_CONTEXT } from "../../form-workspace.context-token.js";
import {
  css,
  html,
  customElement,
  state,
  LitElement,
} from "@umbraco-cms/backoffice/external/lit";
import type { UmbWorkspaceViewElement } from "@umbraco-cms/backoffice/workspace";
import { UmbElementMixin } from "@umbraco-cms/backoffice/element-api";
import type { FormDesign } from "@umbraco-forms/generated";

const elementName = "workspace-view-form-advanced";

@customElement(elementName)
export class UmbWorkspaceViewFormAdvancedElement
  extends UmbElementMixin(LitElement)
  implements UmbWorkspaceViewElement
{
  @state()
  _form?: FormDesign;

  #workspaceContext?: typeof FORMS_FORM_WORKSPACE_CONTEXT.TYPE;

  constructor() {
    super();

    this.consumeContext(FORMS_FORM_WORKSPACE_CONTEXT, (instance) => {
      this.#workspaceContext = instance;
      this.#observeForm();
    });
  }

  #observeForm() {
    if (!this.#workspaceContext) return;

    this.observe(this.#workspaceContext.data, async (form) => {
      if (!form) return;
      this._form = form;
    });
  }

  render() {
    return html`<uui-box>
      <forms-advanced-validation-rules></forms-advanced-validation-rules>
    </uui-box>`;
  }

  static styles = [
    css`
      :host {
        display: block;
        padding: var(--uui-size-layout-1);
      }

      uui-box + uui-box {
        margin-top: var(--uui-size-layout-1);
      }
    `,
  ];
}

export default UmbWorkspaceViewFormAdvancedElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: UmbWorkspaceViewFormAdvancedElement;
  }
}
